ry-color: #ed7d31;\n$secondary-color: #6c5f5b;\n$accent-color: #4f4a45;\n$base-color: #f6f1ee;\n\n/* Mixins */\n// Flex\n@mixin flex($justify, $align, $direction: row, $gap: 0) {\n  display: flex;\n  justify-content: $justify;\n  align-items: $align;\n  flex-direction: $direction;\n  gap: $gap;\n}\n\n// Grid\n@mixin grid($columnTemplate, $gap: 0) {\n  display: grid;\n  grid-template-columns: $columnTemplate;\n  gap: $gap;\n}\n\n// Button\n@mixin clickable($padding, $color, $backgroundColor, $fontSize) {\n  padding: $padding;\n  color: $color;\n  background-color: $backgroundColor;\n  border: none;\n  font-size: $fontSize;\n  cursor: pointer;\n}\n\n// Resetting All Elements Styles\n* {\n  padding: 0;\n  margin: 0;\n  font-family: $primary-font;\n  box-sizing: border-box;\n}\n\nhtml {\n  scroll-behavior: smooth;\n}\n\n#skip-to-content {\n  position: absolute;\n  top: -50px;\n  left: -50px;\n  padding: 8px 12px;\n  background-color: $primary-color;\n  color: $base-color;\n  border: none;\n  cursor: pointer;\n  border-radius: 2px;\n  transition: all 0.4s ease-in-out;\n  z-index: 99;\n}\n\n#skip-to-content:focus {\n  top: 15%;\n  left: 50%;\n  transform: translate(-50%, -15%);\n}\n\n/* Start of Header*/\nheader {\n  position: absolute;\n  inset: 0;\n  width: 100%;\n  height: 80px;\n  background-color: transparent;\n  padding: 0 25px;\n  z-index: 99;\n\n  .navbar {\n    @include flex(space-between, center);\n    height: 100%;\n\n    .nav-brand {\n      color: $base-color;\n      span {\n        color: $primary-color;\n      }\n    }\n    .nav-links {\n      position: absolute;\n      top: 0;\n      right: 0;\n      width: 100%;\n      height: max-content;\n      padding: 30px 0;\n      display: none;\n      background-color: $accent-color;\n      list-style: none;\n      transition: all 0.2s ease-in-out;\n\n      .nav-links_item a {\n        padding: 14px;\n        text-decoration: none;\n        color: $base-color;\n        font-family: $secondary-font;\n        font-weight: $semibold-weight;\n        transition: all 0.2s ease-out;\n      }\n      .nav-links_item a:hover {\n        color: $primary-color;\n      }\n    }\n    .nav-btn {\n      @include clickable(12px 14px, $base-color, transparent, 22px);\n    }\n  }\n  .navbar.active {\n    .nav-links {\n      @include flex(center, center, column, 15px);\n      transform: translateY(80px);\n    }\n  }\n}\nheader:has(.navbar.active) {\n  background-color: $accent-color;\n}\n/* End of Header */\n\n/* Start of Main */\nmain {\n  width: 100%;\n\n  // Start of Hero Section\n  .hero {\n    width: 100%;\n    height: 100vh;\n    padding: 0 25px;\n    @include flex(center, center, column, 150px);\n    background-image: url(\"../public/images/hero-image_2.jpg\");\n    background-repeat: no-repeat;\n    background-size: cover;\n    background-position-x: 30%;\n\n    &_description {\n      color: $base-color;\n\n      p {\n        font-size: 1.2rem;\n      }\n      h1 {\n        font-size: 2.2rem;\n        font-weight: $semibold-weight;\n        font-family: $secondary-font;\n\n        span {\n          color: $primary-color;\n          font-weight: $bold-weight;\n        }\n      }\n    }\n    .explore {\n      @include clickable(10px 8px, $base-color, $primary-color, 1rem);\n      border: $primary-color 1px solid;\n      transition: all 0.1s ease-in-out;\n      text-decoration: none;\n    }\n    .explore:hover {\n      color: $primary-color;\n      background-color: $base-color;\n      border: $primary-color 1px solid;\n    }\n    .explore:active {\n      background-color: #ccc7c4;\n    }\n  }\n  // End of Hero Section\n\n  // Start of Top Kitchen Section\n  .top-kitchen {\n    width: 100%;\n    padding: 36px 25px;\n\n    &_title {\n      color: $primary-color;\n      font-size: 1.5rem;\n      font-family: $secondary-font;\n      font-weight: $medium-weight;\n\n      span {\n        color: $accent-color;\n      }\n    }\n    &_list {\n      @include grid(repeat(1, 1fr), 12px);\n      padding: 24px 0;\n\n      .top-kitchen_item {\n        border: $accent-color 3px solid;\n        border-radius: 8px;\n        position: relative;\n        background-color: $base-color;\n\n        .top-kitchen_item_rating {\n          position: absolute;\n          top: 0;\n          right: 0;\n          @include flex(center, center, row, 8px);\n          padding: 4px 8px;\n          background-color: $accent-color;\n          color: $base-color;\n          font-size: 0.8rem;\n          border-bottom-left-radius: 4px;\n\n          i {\n            color: #ffce31;\n          }\n        }\n        .top-kitchen_item_img {\n          width: 100%;\n          height: 150px;\n          object-fit: cover;\n          border-radius: 4px 4px 0 0;\n        }\n        .top-kitchen_item_description {\n          padding: 8px 16px;\n          margin-bottom: 6px;\n\n          .kitchen-name {\n            font-size: 1rem;\n            font-weight: $medium-weight;\n            font-family: $primary-font;\n            color: $primary-color;\n          }\n          .kitchen-city {\n            font-size: 0.8rem;\n            font-weight: $regular-weight;\n            line-height: 10px;\n            color: $secondary-color;\n          }\n        }\n      }\n    }\n  }\n  // End of Top Kitchen Section\n\n  // Start of About Section\n  .about {\n    width: 100%;\n    padding: 24px 25px;\n    background-color: $secondary-color;\n\n    &_title {\n      color: $base-color;\n      font-size: 1.5rem;\n      font-family: $secondary-font;\n      font-weight: $medium-weight;\n\n      span {\n        color: $primary-color;\n      }\n    }\n    &_description {\n      @include grid(repeat(2, 50%), 12px);\n      padding: 12px 0;\n      grid-template-areas:\n        \"img content\"\n        \"feature feature\";\n\n      .about_description_img {\n        width: 100%;\n        height: 100%;\n        object-fit: cover;\n        grid-area: img;\n      }\n      .about_description_content {\n        @include flex(center, center, column, 12px);\n        padding-bottom: 24px;\n        grid-area: content;\n\n        p {\n          span {\n            font-size: 1.5rem;\n          }\n          color: $base-color;\n          font-size: 0.8rem;\n          font-weight: $regular-weight;\n          line-height: 1.2rem;\n        }\n        button {\n          @include clickable(11px 12px, $base-color, $primary-color, 0.9rem);\n          border: $primary-color 1px solid;\n          transition: all 0.1s ease-in-out;\n        }\n        button:hover {\n          color: $primary-color;\n          background-color: $base-color;\n          border: $primary-color 1px solid;\n        }\n        button:active {\n          background-color: #ccc7c4;\n        }\n      }\n      .about_description_feature {\n        grid-area: feature;\n        justify-self: center;\n        margin-top: 8px;\n\n        span {\n          padding: 6px 10px;\n          font-size: 10px;\n          font-family: $secondary-font;\n          font-weight: $medium-weight;\n          color: $primary-color;\n          background-color: $base-color;\n          border: $primary-color 1px solid;\n          border-radius: 16px;\n        }\n      }\n    }\n  }\n  // End of About Section\n\n  // Start of All Kitchen\n  .all-kitchen {\n    width: 100%;\n    padding: 36px 25px;\n\n    &_title {\n      color: $primary-color;\n      font-size: 1.5rem;\n      font-family: $secondary-font;\n      font-weight: $medium-weight;\n\n      span {\n        color: $accent-color;\n      }\n    }\n    &_list {\n      @include grid(repeat(1, 1fr), 12px);\n      padding: 24px 0;\n\n      .all-kitchen_item {\n        border: $accent-color 3px solid;\n        border-radius: 8px;\n        position: relative;\n        background-color: $base-color;\n\n        .all-kitchen_item_rating {\n          position: absolute;\n          top: 0;\n          right: 0;\n          @include flex(center, center, row, 8px);\n          padding: 4px 8px;\n          background-color: $accent-color;\n          color: $base-color;\n          font-size: 0.8rem;\n          border-bottom-left-radius: 4px;\n\n          i {\n            color: #ffce31;\n          }\n        }\n        .all-kitchen_item_img {\n          width: 100%;\n          height: 150px;\n          object-fit: cover;\n          border-radius: 4px 4px 0 0;\n        }\n        .all-kitchen_item_description {\n          padding: 8px 16px;\n          margin-bottom: 6px;\n\n          .kitchen-name {\n            font-size: 1rem;\n            font-weight: $medium-weight;\n            font-family: $primary-font;\n            color: $primary-color;\n          }\n          .kitchen-city {\n            font-size: 0.8rem;\n            font-weight: $regular-weight;\n            line-height: 10px;\n            color: $secondary-color;\n          }\n          .kitchen-description {\n            font-size: 1rem;\n            font-weight: $light-weight;\n            color: $secondary-color;\n            margin-top: 8px;\n          }\n        }\n      }\n    }\n  }\n  // End of All Kitchen\n}\n/* End of Main */\n\n/* Start of Footer */\nfooter {\n  width: 100%;\n  height: 60px;\n  @include flex(center, center, row, 0);\n  background-color: $secondary-color;\n\n  p {\n    color: $base-color;\n\n    span {\n      color: $primary-color;\n    }\n  }\n}\n/* End of Footer */\n\n/* Start of Media Query */\n// Tablet Media Query\n@media screen and (min-width: 768px) {\n  /* Start of Header Query */\n  header {\n    padding: 0 56px;\n\n    .navbar {\n      .nav-links {\n        width: 100%;\n        position: inherit;\n        @include flex(flex-end, center, row, 80px);\n        background-color: transparent;\n      }\n      .nav-btn {\n        display: none;\n      }\n    }\n  }\n  /* End of Header Media Query */\n\n  /* Start of Main Query */\n  main {\n    /* Start of Hero Section Query */\n    .hero {\n      padding: 0 56px;\n      gap: 120px;\n      background-position-x: 10%;\n\n      &_description {\n        text-align: center;\n        h1 {\n          text-align: center;\n        }\n      }\n    }\n    /* End of Hero Section Query */\n\n    /* Start of Top Kitchen Query */\n    .top-kitchen {\n      padding: 36px 56px;\n\n      &_list {\n        @include grid(repeat(2, 1fr), 24px);\n      }\n    }\n    /* End of Top Kitchen Query */\n\n    /* Start of About Section Query */\n    .about {\n      padding: 36px 56px;\n\n      &_description {\n        .about_description_content {\n          p {\n            span {\n              font-size: 1.8rem;\n            }\n            font-size: 1rem;\n            line-height: normal;\n          }\n        }\n        .about_description_feature {\n          span {\n            padding: 8px 12px;\n            margin: 0 8px;\n            font-size: 12px;\n          }\n        }\n      }\n    }\n    /* End of About Section Query */\n\n    /* Start of All Kitchen Query */\n    .all-kitchen {\n      padding: 36px 56px;\n\n      &_list {\n        @include grid(repeat(2, 1fr), 24px);\n      }\n    }\n    /* End of All Kitchen Query */\n  }\n  /* End of Main Query */\n}\n\n// Laptop Media Query\n@media screen and (min-width: 1024px) {\n  /* Start of Header Query */\n  header {\n    padding: 0 80px;\n\n    .navbar {\n      .nav-brand {\n        font-size: 2rem;\n      }\n      .nav-links {\n        .nav-links_item a {\n          font-size: 1.2rem;\n        }\n      }\n    }\n  }\n  /* End of Header Query */\n\n  /* Start of Main Query */\n  main {\n    /* Start of Hero Section Query */\n    .hero {\n      padding: 0 80px;\n      gap: 100px;\n\n      &_description {\n        p {\n          font-size: 1.6rem;\n        }\n        h1 {\n          font-size: 2.6rem;\n        }\n      }\n      button {\n        @include clickable(12px 10px, $base-color, $primary-color, 1.2rem);\n      }\n    }\n    /* End of Hero Section Query */\n\n    /* Start of Top Kitchen Query */\n    .top-kitchen {\n      padding: 36px 80px;\n\n      &_title {\n        font-size: 2rem;\n        font-weight: $semibold-weight;\n      }\n      &_list {\n        @include grid(repeat(3, 1fr), 24px);\n\n        .top-kitchen_item {\n          .top-kitchen_item_description {\n            .kitchen-name {\n              font-size: 1.4rem;\n            }\n            .kitchen-city {\n              font-size: 14px;\n            }\n          }\n        }\n      }\n    }\n    /* End of Top Kitchen Query */\n\n    /* Start of About Section Query */\n    .about {\n      padding: 24px 80px;\n      &_title {\n        font-size: 2rem;\n        font-weight: $semibold-weight;\n      }\n      &_description {\n        padding: 24px 0;\n\n        .about_description_content {\n          p {\n            span {\n              font-size: 2.2rem;\n            }\n            font-size: 1.2rem;\n            line-height: normal;\n          }\n          button {\n            @include clickable(8px 14px, $base-color, $primary-color, 1.2rem);\n            margin-top: 24px;\n          }\n        }\n        .about_description_feature {\n          margin-top: 24px;\n\n          span {\n            padding: 10px 14px;\n            margin: 0 10px;\n            font-size: 14px;\n          }\n        }\n      }\n    }\n    /* End of About Section Query */\n\n    /* Start of All Kitchen Section Query */\n    .all-kitchen {\n      padding: 36px 80px;\n\n      &_title {\n        font-size: 2rem;\n        font-weight: $semibold-weight;\n      }\n      &_list {\n        @include grid(repeat(3, 1fr), 24px);\n\n        .all-kitchen_item {\n          .all-kitchen_item_description {\n            .kitchen-name {\n              font-size: 1.4rem;\n            }\n          }\n        }\n      }\n    }\n    /* End of Kitchen Section Query */\n  }\n  /* End of Main Query */\n}\n\n// Desktop Media Query\n@media screen and (min-width: 1440px) {\n  /* Start of Main Query */\n  main {\n    /* Start of Hero Section Query */\n    .hero {\n      padding: 0 80px;\n      gap: 40px;\n\n      &_description {\n        width: 60%;\n\n        h1 {\n          font-weight: $bold-weight;\n        }\n      }\n      button {\n        @include clickable(12px 10px, $base-color, $primary-color, 1.2rem);\n      }\n    }\n    /* End of Hero Section Query */\n\n    /* Start of Top Kitchen Query */\n    .top-kitchen {\n      &_title {\n        font-weight: $bold-weight;\n      }\n    }\n    /* End of Top Kitchen Query */\n\n    /* Start of About Section Query */\n    .about {\n      &_title {\n        font-weight: $bold-weight;\n      }\n      &_description {\n        .about_description_content {\n          p {\n            span {\n              font-size: 2.8rem;\n              font-weight: $bold-weight;\n            }\n            font-size: 1.4rem;\n            line-height: normal;\n          }\n          button {\n            @include clickable(8px 14px, $base-color, $primary-color, 1.2rem);\n            margin-top: 24px;\n          }\n        }\n        .about_description_feature {\n          margin-top: 24px;\n\n          span {\n            padding: 10px 14px;\n            margin: 0 10px;\n            font-size: 14px;\n          }\n        }\n      }\n    }\n    /* End of About Section Query */\n\n    /* Start of All Kitchen Section Query */\n    .all-kitchen {\n      padding: 36px 80px;\n\n      &_title {\n        font-size: 2rem;\n        font-weight: $semibold-weight;\n      }\n      &_list {\n        @include grid(repeat(3, 1fr), 24px);\n\n        .all-kitchen_item {\n          .all-kitchen_item_description {\n            .kitchen-name {\n              font-size: 1.4rem;\n            }\n          }\n        }\n      }\n    }\n    /* End of Kitchen Section Query */\n  }\n  /* End of Main Query */\n}\n\n/* End of Media Query */\n"],"sourceRoot":""}]);
